import Storage from 'web-storage-cache'

const localStorage = new Storage()

export function setLocalStorage(key, value) {
  return localStorage.set(key, value)
}

export function getLocalStorage(key) {
  return localStorage.get(key)
}

export function removeLocalStorage(key) {
  return localStorage.delete(key)
}

export function clearLocalStorage() {
  return localStorage.clear()
}

export function setBookObject(fileName, key, value) {
  let book = getLocalStorage(`${fileName}-info`)
  if (!book) {
    book = {}
  }
  book[key] = value
  setLocalStorage(`${fileName}-info`, book)
}

export function getBookObject(fileName, key) {
  let book = getLocalStorage(`${fileName}-info`)
  if (book) {
    return book[key]
  } else {
    return null
  }
}

export function setFontFamily(fileName, fontFamily) {
  setBookObject(fileName, 'fontFamily', fontFamily)
}

export function getFontFamily(fileName) {
  return getBookObject(fileName, 'fontFamily')
}

export function setFontSize(fileName, fontSize) {
  setBookObject(fileName, 'fontSize', fontSize)
}
export function getFontSize(fontSize) {
  return getBookObject(fontSize, 'fontSize')
}

export function setLocale(locale) {
  setLocalStorage('locale', locale)
}
export function getLocale() {
  return getLocalStorage('locale')
}

export function setTheme(fileName, theme) {
  setBookObject(fileName, 'theme', theme)
}
export function getTheme(fileName) {
  return getBookObject(fileName, 'theme')
}

export function setLocation(fileName, location) {
  setBookObject(fileName, 'location', location)
}
export function getLocation(fileName) {
  return getBookObject(fileName, 'location')
}

export function setReadTime(fileName, readTime) {
  setBookObject(fileName, 'readTime', readTime)
}
export function getReadTime(fileName) {
  return getBookObject(fileName, 'readTime')
}

export function setBookMark(fileName, bookMark) {
  setBookObject(fileName, 'bookMark', bookMark)
}
export function getBookMark(fileName) {
  return getBookObject(fileName, 'bookMark')
}

export function saveBookShelf(shelf) {
  return setLocalStorage('shelf', shelf)
}

export function getBookShelf() {
  return getLocalStorage('shelf')
}
